import React, { useState } from 'react';
import PropTypes from 'prop-types';

function Finder__ToolTip(props) {
    const toggleLabel = () => setShow(!show);

    const [show, setShow] = useState(false);

    const {
        thumbnailSourceName,
        thumbnailSourceUrl,
        thumbnailAuthorName,
        thumbnailAuthorUrl,
        thumbnailLicenseType,
        thumbnailLicenseUrl,
        displayToolTip,
    } = props;

    const labelAuthor =
            thumbnailAuthorUrl && thumbnailAuthorName ? (
                <a href={thumbnailAuthorUrl}>{thumbnailAuthorName}</a>
            ) : thumbnailAuthorName ? (
                <span>{thumbnailAuthorName}</span>
            ) : null,
        labelSource =
            thumbnailSourceUrl && thumbnailSourceName ? (
                <a href={thumbnailSourceUrl}>{thumbnailSourceName}</a>
            ) : thumbnailSourceName ? (
                <span>{thumbnailSourceName}</span>
            ) : null,
        labelLicense =
            thumbnailLicenseUrl && thumbnailLicenseType ? (
                <a href={thumbnailLicenseUrl}>{thumbnailLicenseType}</a>
            ) : thumbnailLicenseType ? (
                thumbnailLicenseType
            ) : null;

    if (displayToolTip && (thumbnailAuthorName || thumbnailSourceName)) {
        return (
            <figure className="wrapper--tooltip__label" data-open={show}>
                <button
                    className="tooltip tooltip--image-credit"
                    type="button"
                    aria-expanded={show}
                    onClick={toggleLabel}
                >
                    <span
                        className={`tooltip__icon icon fa-sharp fa-light fa-subtitles ${
                            show ? 'fa-xmark' : 'fa-subtitles'
                        }`}
                    />
                    <span className="sr-only">Show image credit</span>
                </button>
                <figcaption className="tooltip__label">
                    {labelAuthor} {labelSource && '/ '} {labelSource}{' '}
                    {labelLicense}
                </figcaption>
            </figure>
        );
    } else {
        return null;
    }
}

Finder__ToolTip.propTypes = {
    thumbnailSourceName: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    thumbnailSourceUrl: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    thumbnailAuthorName: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    thumbnailAuthorUrl: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    thumbnailLicenseType: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    thumbnailLicenseUrl: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    displayToolTip: PropTypes.bool,
};

export default Finder__ToolTip;
