import PropTypes from 'prop-types';
import React from 'react';

/**
 * Render browse people.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Search__Results__BrowsePeople(props) {
    return (
        <li className="cta-block__cta">
            <a
                className="outline-cta--bright-square"
                href={
                    'https://www.citystgeorges.ac.uk/about/people/' +
                    props.path +
                    '?query=' +
                    props.query
                }
            >
                {props.type.charAt(0).toUpperCase() + props.type.slice(1)}
            </a>
        </li>
    );
}

Search__Results__BrowsePeople.propTypes = {
    query: PropTypes.string,
    type: PropTypes.string,
    path: PropTypes.string,
};

export default Search__Results__BrowsePeople;
